<template>
  <li>
    <div :class="{ plus: isFolder || hasChild }" @click="toggle">
      <span v-if="isFolder || hasChild">[{{ isOpen ? "-" : "+" }}]</span>
      {{ item.user_name }}
      <span v-if="item.referrals || child.referrals">, рефералов:</span>
      {{ item.referrals || child.referrals }}
    </div>

    <ul
      v-show="isOpen"
      v-if="(isFolder && this.child.user_id !== this.item.user_id) || hasChild"
    >
      <!-- <tr> -->
      <ItemList
        class="item"
        v-for="(child, index) in item.referral_users || child.referral_users"
        :key="index"
        :item="child"
      ></ItemList>
    </ul>
    <!-- </tr>
    </table> -->
  </li>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ItemList",
  props: {
    item: Object,
  },

  data: function () {
    return {
      isOpen: false,
      child: {},
    };
  },
  computed: {
    ...mapGetters(["USERS"]),
    isFolder: function () {
      return this.item.referral_users && this.item.referral_users.length;
    },
    hasChild: function () {
      let isChild = false;
      this.USERS.forEach((u) => {
        if (this.item.user_id === u.user_id) {
          this.child = u;
          isChild = true;
        }
      });
      return isChild;
    },
  },
  methods: {
    toggle: function () {
      if (this.isFolder || this.hasChild) {
        this.isOpen = !this.isOpen;
      }
    },
  },
};
</script>

